const onStyle = {
  "background-color": "blue"
};

const style = {
  "background-color": "#333"
};

const steps = step => {
  const steps = [...Array(8)];
  return steps.map((_, i) => {
    return (
      <div className="column" style={step == i + 1 ? onStyle : {}}>
        <div style={style} />
      </div>
    );
  });
};

const Track = props => <div className="columns">{steps(props.step)}</div>;

export default Track;
